/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*::before,
*:after {
  box-sizing: border-box; }

body {
  line-height: 1.5;
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

a {
  text-decoration: none; }

.mb-1 {
  margin-bottom: 8px; }

.mb-2 {
  margin-bottom: 16px; }

.mb-3 {
  margin-bottom: 24px; }

.mb-4 {
  margin-bottom: 32px; }

.mb-6 {
  margin-bottom: 48px; }

.d-block {
  display: block; }

.h1 {
  font-size: 4rem; }

.h2 {
  font-size: 3rem; }

.h3 {
  font-size: 2.625rem; }

.h4 {
  font-size: 2.25rem; }

.h5 {
  font-size: 2rem; }

.h6 {
  font-size: 1.5rem; }

.lg-text {
  font-size: 1.25rem; }

.md-text {
  font-size: 1.125rem; }

.sm-text {
  font-size: 0.875rem; }

.font-bold {
  font-weight: bold; }

.bg-primary {
  background-color: #AA0601; }

.bg-primary-light {
  background-color: #FBF2F2; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px; }

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.header .logo {
  display: block;
  width: 80px;
  padding-top: 16px;
  padding-bottom: 16px; }

.header .menu {
  display: flex;
  flex-wrap: wrap; }

.header .menu-link {
  display: block;
  padding: 25px 32px;
  color: #fff; }

.header .pr-0 {
  padding-right: 0; }

@media (max-width: 767px) {
  .header .container {
    padding: 0; }
  .header h1 {
    width: 100%; }
  .header .logo {
    padding-top: 12px;
    padding-bottom: 12px;
    margin: 0 auto; }
  .header .menu {
    width: 100%; }
  .header li {
    width: 50%;
    border: 1px solid #fff;
    text-align: center;
    font-size: 1rem; }
  .header .menu-link {
    padding: 12px; } }

.footer {
  color: #fff; }
  .footer a {
    display: block;
    color: #fff; }
  .footer-header, .footer-menu,
  .footer .social-media-group, .footer-footer,
  .footer .policy {
    display: flex;
    justify-content: space-between; }
  .footer-header {
    padding-top: 48px;
    padding-bottom: 32px;
    border-bottom: 1px solid #fff; }
  .footer-menu {
    align-items: center;
    margin-bottom: 24px;
    width: 448px;
    height: 48px; }
  .footer .contact-method span {
    vertical-align: middle;
    padding-right: 20px; }
  .footer .social-media-group {
    width: 208px; }
  .footer .social-media-logo {
    width: 48px; }
  .footer-footer {
    padding-top: 32px;
    padding-bottom: 32px; }
  .footer .policy {
    width: 174px;
    align-items: center; }
  @media (max-width: 992px) {
    .footer-menu {
      height: 40px; }
    .footer .social-media-group {
      width: 152px; }
    .footer .social-media-logo {
      width: 40px; } }
  @media (max-width: 767px) {
    .footer-header {
      padding-top: 24px;
      padding-bottom: 24px; }
    .footer-footer,
    .footer .policy {
      flex-direction: column;
      align-items: start; }
    .footer .policy li {
      margin-top: 8px; }
    .footer-menu {
      display: none; }
    .footer .social-media-group {
      width: 88px; }
    .footer .social-media-logo {
      width: 24px; }
    .footer-footer {
      padding-top: 16px;
      padding-bottom: 24px; }
    .footer .h4,
    .footer .sm-text {
      font-size: 1rem; } }

.banner {
  background-image: url(../images/home-header.png);
  height: 90vh;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  color: #650300;
  display: flex;
  align-items: center;
  text-align: right; }
  .banner .container {
    width: 100%; }
  .banner .sm-button {
    padding: 6px 12px;
    border: none;
    background-color: #AA0601; }
    .banner .sm-button a {
      color: #fff;
      font-size: 1.25rem; }

main section {
  padding-top: 80px;
  padding-bottom: 80px; }

.introduction .intro-title {
  color: #650300;
  text-align: center; }

.introduction .intro-card-box {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px; }

.introduction .intro-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 12px;
  width: 25%; }

.introduction .card-header {
  width: 100px; }

.introduction .card-body {
  color: #650300; }

.classic .classic-title {
  color: #650300;
  text-align: center; }

.classic .classic-card-box {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px; }

.classic .classic-card {
  padding-left: 12px;
  padding-right: 12px;
  width: 33.33%; }

.classic .card-title {
  text-align: center;
  color: #AA0601;
  font-style: italic; }

.joint {
  background-image: url(../images/index-bg.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }
  .joint .joint-title {
    color: #fff;
    text-align: center; }
  .joint .joint-card-box {
    display: flex;
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px; }
  .joint .joint-card {
    width: 50%; }
  .joint .card-photo-1 {
    background-image: url(../images/home-section4-1.png); }
  .joint .card-photo-2 {
    background-image: url(../images/home-section4-2.png); }
  .joint .card-photo-1,
  .joint .card-photo-2 {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    min-height: 240px; }
  .joint .card-title {
    background-color: #fff;
    text-align: center;
    color: #AA0601;
    font-style: italic; }

.comment .comment-title {
  color: #650300;
  text-align: center; }

.comment .comment-card-box {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px; }

.comment .comment-card {
  padding-left: 12px;
  padding-right: 12px;
  width: 25%; }

.comment .card-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px;
  height: 205px;
  border: 1px solid #DEE2E6;
  border-radius: 0 0 4px 4px;
  box-shadow: 0px 2px 6px #00000029; }

.comment .comment-date {
  font-size: 0.875rem;
  color: #707070; }
